@mixin laptop {
  @media screen and (max-width: 1000px) {
    @content
  }
}

@mixin tablet {
  @media screen and (max-width: 700px) {
    @content
  }
}
@mixin mobile {
  @media screen and (max-width: 500px) {
    @content
  }
}

@mixin mobile-sm {
  @media screen and (max-width: 320px) {
    @content
  }
}

@font-face {
  font-family: rock;
  src: url(../fonts/Rockstar-ExtraBold.otf);
}

html {
  scroll-behavior: smooth;
  position: relative;
}

body {
  height: 600vh;
  background-color: rgb(22, 22, 22);
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.logo-url {
  text-decoration: none;
}

.logo {
  font-size: 1.5rem !important;
}

.greeting--text {
  font-family: "rock";
  font-size: 5rem;
  @include tablet {
    font-size: 4rem;
  }
  @include mobile {
    font-size: 4rem;
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}

.header--main {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  background-color: rgb(34, 32, 32);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 90px;
}

.header--main {
  color: white;
}

.logo {
  font-size: 1.5rem;
  margin-left: 30px;
  cursor: pointer;
  color: white;
  background-image: linear-gradient(90deg, rgb(104, 224, 98), rgb(48, 164, 50));
  background-clip: text;
  color: transparent;
  font-weight: 700;
  text-shadow: none;
}

.header--main > nav {
  padding: 30px;
  display: flex;
  align-items: center;
  text-align: center;
  list-style: none;
  font-size: 1.5rem;
  color: white;
  text-shadow: none;
}

.header--main > nav > a {
  margin: 20px;
  text-decoration: none;
  color: white;
  @include tablet {
    display: none;
  }
}

.header--main nav li {
  transition: all 0.15s;
}

.header--main nav li:hover {
  text-decoration: underline;
  text-underline-position: under;
  padding-bottom: 0.5em;
  text-decoration-color: rgb(48, 164, 50);
}

#hamburger-menu {
  display: none;
  @include tablet {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 35px;
    right: 20px;
  }

  .menu {
    width: 38px;
    height: 3px;
    background-color: white;
    margin: 3px;
  }
}

#overlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;

  nav {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    list-style: none;
    font-size: 2rem;
    display: flex;
    flex-direction: column;
    gap: 30px;

    a {
      display: block;
      text-decoration: none;
      color: white;
      width: fit-content;
    }
  }
}

main {
  width: 90%;
  margin: 0;
  margin-top: 50px;
  text-shadow: 2px 0 #000000, -2px 0 #000000, 0 2px #000000, 0 -2px #000000,
    1px 1px #000000, -1px -1px #000000, 1px -1px #000000, -1px 1px #000000;
}

main h2 {
  font-family: rock;
  color: rgb(26, 177, 54);
  text-align: center;
  font-size: 3rem;
  @include laptop {
    margin: 0px;
  }
}

main h3 {
  text-align: center;
}

main .greeting {
  position: absolute;
  left: 0;
  height: 100vh;
  width: 100%;
  background-image: url("../images/matrix-g7eee74277_1920.jpg");
  background-repeat: repeat-y;
  background-size: cover;
  @include mobile {
    width: 103vw;
  }
  @include mobile-sm {
    width: 112vw;
  }
}

main .greeting--image {
  object-fit: contain;
  object-position: right;
  width: 50%;
}

main .greeting--text {
  color: white;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.8);
  padding: 50px;
  @include mobile {
    padding: 0;
  }
}

.about-me p {
  text-align: left;
}

.about-section {
  font-size: 1.6rem;
  text-align: left;
  margin-top: 100vh;
}

.long-story-btn {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-top: 30px;
  margin-bottom: 30px;
  background-color: transparent;
  color: white;
  font-size: 1.6rem;
  border: 1px solid white;
  padding: 10px;
  border-radius: 5px;
  transition: all 0.15s;
  
  img {
    width: 40px;
    height: 40px;
  }

  &:hover {
    transform: scale(1.1);
  }
}

.long-story-text {
  margin: 0;
}

.grid-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.work-item {
  display: flex;
  align-items: center;
  justify-content: center;
  @include mobile {
    width: 100vw;
  }
  @include mobile-sm {
    display: block;
    width: 80vw;
  }
}

.project {
  display: flex;
  align-items: center;
  margin: auto;
  margin: 50px;
  flex: 1;
  @include laptop {
    display: flex;
    flex-direction: column;
  }
}

.project img {
  width: 50%;
  @include laptop {
    width: 90vw;
  }
  @include mobile {
    width: 90vw;
  }
}

.project-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex: 1;
}

.project-description p {
  width: 60%;
  font-size: 20px;
  @include tablet {
    width: 90%;
  }
}

.project-description h2 {
  margin-top: 0px;
  font-size: 33px;
  @include laptop {
    padding: 20px;
  }
}

.project-button {
  width: 70%;
  display: flex;
  justify-content: space-evenly;
  gap: 10px;

  a {
    text-decoration: none;
  }
}

.project-button button {
  display: flex;
  align-items: center;
  font-size: 17px;
  padding: 5px 20px;
  font-weight: 700;
  border: none;
  background-color: green;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}

.project-button button:hover {
  opacity: 0.7;
}

.project-button img {
  margin: 0 10px;
  width: 30px;
  height: 30px;
}

.middle {
  transition: 0.7s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center;
  width: 400px;
}

.grid-image {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: 0.5s ease;
  backface-visibility: hidden;

}

.middle button {
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  border-radius: 5px;
  padding: 10px 30px;
  font-size: 15px;
  font-weight: 700;
  border: none;
  transition: transform 0.15s;
  white-space: nowrap;
  background-color: black;
  color: white;
}

.name-of-project {
  margin-bottom: 50px;
  font-size: 30px;
}

.project-name-mobile {
  display: none;
}

.button-mobile {
  display: none;
}

#contact-section {
  margin: 0px;
}

#contact-section h2 {
  margin: 40px;
}

.warning {
  display: flex;
  align-items: center;
  gap: 20px;
}

.warning-image {
  width: 30px;
  height: 30px;
}

.stop {
  display: block;
  font-size: 3rem;
  font-weight: 700;
  color: rgb(204, 0, 0);
}

.gifNeo {
  width: 50%;
  @include tablet {
    width: 75%;
  }
}

.contact h2 {
  text-shadow: 2px 0 #000000, -2px 0 #000000, 0 2px #000000, 0 -2px #000000,
    1px 1px #000000, -1px -1px #000000, 1px -1px #000000, -1px 1px #000000;
}

.contact-part {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  height: 50vh;
  width: 70vw;
  position: relative;
  @include tablet {
    height: 100%;
    align-items: center;
    flex-direction: column-reverse;
  }
}

.contact-links-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-links-container li {
  font-size: 2rem;
  list-style: none;
  padding: 40px;
  display: flex;
  gap: 50px;
  @include laptop {
    display: flex;
    flex-direction: row;
    padding: 10px;
  }
  @include mobile {
    flex-direction: column;
  }
}

.contact-links-container ul {
  padding: 0;
}

.contact-links-container a {
  font-family: rock;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-weight: 700;
  text-decoration: none;
  color: white;
  font-size: 3rem;
  font-weight: 700;
}

.contact-links-container img {
  width: 50px;
  height: 50px;
}

.contact-form-container h3 {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  color: white;
  text-shadow: none;
  margin: 0;
}

.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 320px;
  @include tablet {
    display: block;
  }
  @include mobile-sm {
    width: 260px;
  }
}

#name {
  margin: 0;
}

#user_name,
#user_email,
#message {
  padding: 20px 40px;
  width: 230px;
  background-color: #3d3d3d;
  border: none;
  margin-top: 20px;
  border-radius: 10px;
  color: white;
}

.send-button {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
  margin-top: 20px;
  font-size: 25px;
  padding: 13px 80px 13px 80px;
  border-radius: 10px;
  transition: transform 0.15s;
  white-space: nowrap;
  background-color: rgb(39, 39, 39);
  color: white;
  cursor: pointer;
  box-shadow: inset 3px 4px 0px rgb(104, 104, 104);
  transition: all 0.15s;
}

.send-button:active {
  box-shadow: inset -2px -4px 0px rgb(179, 179, 179);
}

.send-button:hover {
  background-color: rgb(47, 47, 47);
}

#message-send {
  position: fixed;
  bottom: 20px;
  right: 50px;
  background-color: #272727;
  height: 50px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 10px;
  font-size: 20px;
}
